@charset "UTF-8";
@import url(normalize.css);
@font-face {
  font-family: 'PT Sans Narrow';
  src: local("PT Sans Narrow Regular"), local("PT-Sans-Narrow-Regular"), url("../fonts/ptsansnarrow.woff2") format("woff2"), url("../fonts/ptsansnarrow.woff") format("woff");
  font-weight: 400;
  font-style: normal;
  font-display: swap; }

@font-face {
  font-family: 'PT Sans Narrow';
  src: local("PT Sans Narrow Bold"), local("PT-Sans-Narrow-Bold"), url("../fonts/ptsansnarrowbold.woff2") format("woff2"), url("../fonts/ptsansnarrowbold.woff") format("woff2");
  font-weight: 700;
  font-style: normal;
  font-display: swap; }

.visually-hidden:not(:focus):not(:active),
input[type='checkbox'].visually-hidden,
input[type='radio'].visually-hidden {
  position: absolute;
  overflow: hidden;
  clip: rect(0 0 0 0);
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  white-space: nowrap;
  border: 0;
  clip-path: inset(100%); }

body {
  min-width: 960px;
  margin: 0;
  text-transform: uppercase;
  color: #ffffff;
  background-color: black;
  background-image: url("../images/bg.jpg");
  background-repeat: no-repeat;
  background-position: top center;
  font-family: "PT Sans Narrow", "Arial", sans-serif;
  font-size: 14px;
  font-weight: 400;
  line-height: 24px; }

a {
  text-decoration: none; }

img {
  max-width: 100%;
  height: auto; }

.site-header {
  background-color: #000000; }

.site-menu {
  width: 580px;
  display: flex;
  flex-wrap: wrap;
  font-size: 16px;
  line-height: 24px;
  margin: 0;
  padding: 0;
  list-style: none; }

.site-menu__link {
  display: block;
  padding: 25px 20px;
  vertical-align: middle;
  color: #ffffff;
  font-weight: 700; }
  .site-menu__link:hover, .site-menu__link:focus {
    background-color: #333333; }
  .site-menu__link:active {
    background-color: #242424; }

.user-menu {
  display: flex;
  flex-wrap: wrap;
  font-size: 16px;
  line-height: 24px;
  margin: 0;
  padding: 0;
  list-style: none; }

.user-menu__link {
  display: block;
  padding: 25px 20px;
  vertical-align: middle;
  color: #ffffff;
  font-weight: 700; }
  .user-menu__link--icon {
    padding-left: 50px; }
  .user-menu__link:hover, .user-menu__link:focus {
    background-color: #333333; }
  .user-menu__link:active {
    background-color: #242424; }

.features__list {
  color: #ffffff;
  margin: 0;
  padding: 0;
  list-style: none; }

.features__title {
  font-size: 30px;
  line-height: 42px; }

.features__item {
  text-align: center; }

.container {
  width: 940px;
  margin: 0 auto;
  padding: 0 10px; }

.container--bg--light {
  background-color: #f8f5f2;
  background-image: url("../../images/bg-light.jpg");
  background-repeat: repeat;
  background-position: 0 0; }

.container--columns--2 {
  display: flex;
  justify-content: space-between; }

.news {
  color: #000000; }

.news-list {
  color: #000000; }

.gallery {
  color: #000000; }

